import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Link, Navigate } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Container,
  Box,
  Badge,
  Menu,
  MenuItem,
  IconButton,
} from '@mui/material';
import { ShoppingCart as CartIcon, AccountCircle } from '@mui/icons-material';
import Login from './components/Login';
import Register from './components/Register';
import Products from './components/Products';
import Cart from './components/Cart';
import Profile from './components/Profile';
import { CartProvider, useCart } from './context/CartContext';
import './vintage-theme.css';

const NavBar = ({ isLoggedIn, handleLogout }) => {
  const { cartItems } = useCart();
  const [anchorEl, setAnchorEl] = useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar position="static" className="navbar">
      <Toolbar>
        <Typography 
          variant="h6" 
          component={Link} 
          to="/" 
          className="navbar-brand"
          sx={{ flexGrow: 1, textDecoration: 'none' }}
        >
          Vintage Camera Store
        </Typography>
        {isLoggedIn ? (
          <>
            <Button color="inherit" component={Link} to="/products" className="nav-link">
              Products
            </Button>
            <Button
              color="inherit"
              component={Link}
              to="/cart"
              className="nav-link"
              startIcon={
                <Badge badgeContent={cartItems.length} color="error">
                  <CartIcon />
                </Badge>
              }
            >
              Cart
            </Button>
            <IconButton
              size="large"
              color="inherit"
              onClick={handleMenu}
              className="nav-link"
            >
              <AccountCircle />
            </IconButton>
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleClose}
              PaperProps={{
                style: {
                  backgroundColor: '#F5F5DC',
                  border: '2px solid #8B7355'
                }
              }}
            >
              <MenuItem 
                component={Link} 
                to="/profile" 
                onClick={handleClose}
                style={{ fontFamily: 'Old Standard TT, serif' }}
              >
                My Profile
              </MenuItem>
              <MenuItem 
                onClick={() => { handleClose(); handleLogout(); }}
                style={{ fontFamily: 'Old Standard TT, serif' }}
              >
                Logout
              </MenuItem>
            </Menu>
          </>
        ) : (
          <>
            <Button color="inherit" component={Link} to="/login" className="nav-link">
              Login
            </Button>
            <Button color="inherit" component={Link} to="/register" className="nav-link">
              Register
            </Button>
          </>
        )}
      </Toolbar>
    </AppBar>
  );
};

const App = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(!!localStorage.getItem('token'));

  useEffect(() => {
    // Check login status when component mounts
    setIsLoggedIn(!!localStorage.getItem('token'));

    // Add event listener for storage changes
    const handleStorageChange = () => {
      setIsLoggedIn(!!localStorage.getItem('token'));
    };

    window.addEventListener('storage', handleStorageChange);
    return () => window.removeEventListener('storage', handleStorageChange);
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    localStorage.removeItem('cart');
    setIsLoggedIn(false);
    window.location.href = '/login';
  };

  return (
    <CartProvider>
      <Router>
        <Box sx={{ flexGrow: 1 }}>
          <NavBar isLoggedIn={isLoggedIn} handleLogout={handleLogout} />
        </Box>

        <Container>
          <Routes>
            <Route 
              path="/login" 
              element={<Login onLoginSuccess={() => setIsLoggedIn(true)} />} 
            />
            <Route path="/register" element={<Register />} />
            <Route
              path="/products"
              element={
                <PrivateRoute>
                  <Products />
                </PrivateRoute>
              }
            />
            <Route
              path="/cart"
              element={
                <PrivateRoute>
                  <Cart />
                </PrivateRoute>
              }
            />
            <Route
              path="/profile"
              element={
                <PrivateRoute>
                  <Profile />
                </PrivateRoute>
              }
            />
            <Route
              path="/"
              element={<Navigate to="/products" />}
            />
          </Routes>
        </Container>
      </Router>
    </CartProvider>
  );
};

const PrivateRoute = ({ children }) => {
  const token = localStorage.getItem('token');
  return token ? children : <Navigate to="/login" />;
};

export default App; 